@import url(https://fonts.googleapis.com/css?family=Roboto);
@import url(https://fonts.googleapis.com/css?family=Raleway);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* width */
::-webkit-scrollbar {
  width: 10px; }

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 10px; }

/* Handle */
::-webkit-scrollbar-thumb {
  background: #CF5050; }

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; }

html {
  height: 100%; }

* {
  box-sizing: border-box; }

body {
  background: url(/static/media/foto2.2933c3a1.jpg) no-repeat 50% fixed;
  background-size: cover; }

html, body {
  max-width: 100%;
  overflow-x: hidden; }

.app {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 106vh;
  padding: 5%; }

button {
  position: fixed;
  bottom: 0;
  right: 0;
  margin: 0 2% 2% 0;
  height: 40px;
  width: 40px;
  background-color: #CF5050;
  border: none;
  border-radius: 50%;
  transition: opacity 0.2s ease; }
  button:hover {
    cursor: pointer;
    opacity: 0.8; }
  button:focus {
    outline: 0; }
  button i {
    color: white; }

.profile {
  display: flex;
  flex-direction: row;
  margin-bottom: 5%;
  margin-left: -50px; }
  .profile img {
    border-radius: 50%;
    height: 120px;
    width: 120px;
    margin-right: 5%; }
  .profile .profile-info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    border-left-color: #CF5050;
    border-left-width: 1px;
    border-left-style: solid;
    padding-left: 5%;
    width: 350px; }
    .profile .profile-info p {
      color: white;
      letter-spacing: 2px;
      font-weight: bold;
      text-shadow: 1px 1px black;
      line-height: 15px;
      font-family: 'Roboto', sans-serif; }
      .profile .profile-info p:last-child {
        float: right;
        font-size: 10px;
        font-family: 'Raleway', sans-serif; }
  .profile:hover {
    cursor: pointer; }

.icons {
  display: flex;
  flex-direction: row;
  justify-content: space-around; }
  .icons a {
    background: rgba(38, 38, 38, 0.7);
    padding: 10px;
    border-radius: 5px;
    margin-left: 10px; }
  .icons i {
    color: white;
    border-radius: 5px;
    font-size: 40px;
    transition: all 0.1s ease-out; }
    .icons i:hover {
      cursor: pointer; }

.about-me {
  background: url(/static/media/foto4.5604d056.jpg) no-repeat 50% fixed;
  background-size: cover;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center; }
  .about-me .my-name {
    padding-left: 10px;
    padding-right: 10px;
    background: yellow;
    color: black;
    border: 1px solid black;
    border-radius: 3px; }
  .about-me div {
    background: #262626b3;
    height: auto;
    width: 50vw;
    padding: 2%;
    border-radius: 2%;
    font-family: 'Nunito', sans-serif; }
    .about-me div h4 {
      -webkit-background-clip: text;
      margin-bottom: 4%;
      text-align: center;
      color: #CF5050; }
      .about-me div h4 .curly-braces {
        font-weight: bold;
        color: white; }
    .about-me div p {
      color: white;
      text-align: left; }
    .about-me div iframe {
      margin-left: auto;
      display: block;
      margin-right: auto; }

@media screen and (max-width: 420px) {
  .profile {
    margin-left: 0; }
    .profile img {
      margin-right: 0; }
    .profile .profile-info {
      padding-left: 0; } }

